import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Tipo_campo } from '../../../modelo/Tipo_campo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tipo_campo-dados-details',   templateUrl: 'tipo_campo_dadosDetails.component.html' }) export class Tipo_campoDadosDetailsComponent implements OnInit {    tipo_campo: Tipo_campo;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.tipo_campo.Tipo_campo";                 let id = +params['id'];         this.tipo_campo = new Tipo_campo();         this.tipo_campo.cd_tipo_campo = id;         o.objetoJson = this.tipo_campo;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a tipo_campo. '; }      this.tipo_campo = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


