import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'tipo_campo-dados',   templateUrl: 'tipo_campo_dados.component.html' }) export class Tipo_campoDadosComponent implements OnInit {       displayMode: Tipo_campoDadosDisplayModeEnum;     displayModeEnum = Tipo_campoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Tipo_campoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Tipo_campoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Tipo_campoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Tipo_campoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


